import React from 'react';
import RcDialog from 'rc-dialog';
import { Dom } from 'rc-util';
import confirm from './confirm';

function noop() {
}

let mousePosition;
let mousePositionEventBinded;

export default class Dialog extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            confirmLoading: false,
            visible: this.props.visible
        };
    }

    handleCancel() {
        this.props.onCancel();
    }

    handleOk() {
        this.setState({
            confirmLoading: true
        });
        this.props.onOk();
    }

    componentWillReceiveProps(nextProps) {
        if ('visible' in nextProps) {
            let newState = {
                visible: nextProps.visible
            };
            //  loading 
            if (!nextProps.visible) {
                newState.confirmLoading = false;
            }
            this.setState(newState);
        }
    }

    componentDidMount() {
        if (mousePositionEventBinded) {
          return;
        }
        // 
        Dom.addEventListener(document.body, 'click', function onDocumentMousemove(e) {
          mousePosition = {
            x: e.pageX,
            y: e.pageY
          };
          // 20ms 
          //  zoom 
          // 
          setTimeout(() => mousePosition = null, 20);
        });
        mousePositionEventBinded = true;
    }

    render() {
        let loadingClass = this.state.confirmLoading ? ' kuma-btn-loading' : '';
        let props = this.props;
        let defaultFooter = [
            <button key="cancel"
                type="button"
                className="kuma-button kuma-button-swhite" onClick={this.handleCancel.bind(this)}> </button>,
            <button key="confirm"
                type="button"
                className={'kuma-button kuma-button-sblue' + loadingClass}
                onClick={this.handleOk.bind(this)}>
                 
            </button>
        ];
        let footer = props.footer || defaultFooter;
        let visible = this.state.visible;
        return <RcDialog onClose={this.handleCancel.bind(this)} footer={footer} {...props}
            visible={visible} mousePosition={mousePosition} />;
    }
}

Dialog.defaultProps = {
    prefixCls: 'kuma-dialog',
    onOk: noop,
    onCancel: noop,
    width: 520,
    transitionName: false,
    maskAnimation: false
};

Dialog.info = function(props) {
  props.iconClassName = 'kuma-icon-information';
  props.okCancel = false;
  return confirm(props);
};

Dialog.success = function(props) {
  props.iconClassName = 'kuma-icon-success';
  props.okCancel = false;
  return confirm(props);
};

Dialog.error = function(props) {
  props.iconClassName = 'kuma-icon-error';
  props.okCancel = false;
  return confirm(props);
};

Dialog.confirm = function(props) {
  props.okCancel = true;
  return confirm(props);
};



