import React from 'react';
import reactMixin  from 'react-mixin';

class SearchBar extends React.Component {

    constructor(props) {
        super(props);
        this.state= {
          searchTxt:""
        };
    }

    componentDidMount() {

    }

    componentDidUpdate() {

    }
    

    componentWillUnmount () {
       
    }

    prepareStyle() {

    }

    doSearch() {
      this.props.actionBarCB("SEARCH",this.state.searchTxt)
    }

    onKeyDown(e) {
      if(e.keyCode==13) {
        this.doSearch();
      }
    }

    render() {

        let _className=this.props.jsxprefixCls;
        return (<div className={_className}>
            <input type="text" className="kuma-input" placeholder="" valueLink={this.linkState('searchTxt')} onKeyDown={this.onKeyDown.bind(this)} />
            <i className="kuma-icon kuma-icon-search" onClick={this.doSearch.bind(this)}></i>
        </div>);

    }

};

SearchBar.propTypes= {

};

SearchBar.defaultProps = {
    jsxprefixCls: "kuma-grid-searchbar"
};

reactMixin.onClass(SearchBar,React.addons.LinkedStateMixin);

export default SearchBar;



